.filter_wrapper {
  min-height: calc(100vh - 634px);
  padding   : 30px 0px 0px;
  color     : var(--brand_color_black);

  .filter_breadcrumb_area {
    display        : flex;
    justify-content: space-between;
    align-items    : flex-end;

  }

  .filter_body {
    padding-top: 25px;

    .filter_form {
      margin-top    : 25px;
      border        : 1px solid var(--gray6);
      border-radius : 5px;
      padding-bottom: 12px;

      .filter_submit_btn {
        border          : none;
        border-radius   : 5px;
        background-color: var(--brand_color_red);
        color           : #fff;
        font-size       : 16px;
        font-family     : "GothamPro-Medium";
        line-height     : 15px;
        text-align      : center;
        outline         : none;
        padding         : 12px 0;
        width           : stretch;
        margin          : 30px 16px 0px;
        cursor          : pointer;
      }
    }

    .right_top {
      display        : flex;
      justify-content: space-between;
      margin-bottom  : -15px;

      .right_top_filter {
        display: flex;

        button {
          border          : none;
          background-color: transparent;
          cursor          : pointer;
          display         : flex;
          align-items     : center;

          img {
            margin-right: 8px;
          }
        }

        .by_popular {
          margin-left: 30px;
        }
      }

      .right_top_change_grid {
        button {
          border          : none;
          background-color: transparent;
          cursor          : pointer;

          &:last-child {
            margin-left: 5px;
          }
        }
      }
    }

    .button_area {
      display        : flex;
      justify-content: center;
      margin-top     : 60px;
    }

    .pagination_area {
      display        : flex;
      justify-content: center;
      margin-top     : 30px;
    }

  }

}